import * as Hilo from 'hilojs';
import merge from 'lodash-es/merge';

export default class Bird extends Hilo.Sprite {
  public startX: number = 0; // x
  public startY: number = 0; // y
  public groundY: number = 0; // 
  public gravity: number = 0; // 
  public flyHeight: number = 0; // 
  public initVelocity: number = 0; // 

  public isDead: boolean = true; // 
  public isUp: boolean = false; // 

  public flyStartY: number = 0; // y
  public flyStartTime: number = 0; // 

  private tween: any[] | Hilo.Tween;

  constructor(properties: any) {
    super(properties);
    merge(this, properties);

    this.addFrame(properties.atlas.getSprite('bird'));
    this.interval = 6;
    this.pivotX = 43;
    this.pivotY = 30;

    this.gravity = 10 / 1000 * 0.3;
    this.flyHeight = 80;
    this.initVelocity = Math.sqrt(2 * this.flyHeight * this.gravity);
  }

  public getReady() {
    // 
    this.x = this.startX;
    this.y = this.startY;

    this.rotation = 0;
    this.interval = 6;
    this.play();
    this.tween = Hilo.Tween.to(
      this,
      { y: this.y + 10, rotation: -8 },
      { time: 400, reverse: true, loop: true }
    );
  }

  public startFly() {
    this.isDead = false;
    this.interval = 3;
    this.flyStartY = this.y;
    this.flyStartTime = +new Date();

    if (this.tween && this.tween instanceof Hilo.Tween) {
      this.tween.stop();
    }
  }

  public onUpdate = () => {
    if (this.isDead) {
      return;
    }

    // 
    const time = +new Date() - this.flyStartTime;
    // 
    const distance =
      this.initVelocity * time - 0.5 * this.gravity * time * time;
    // y
    const y = this.flyStartY - distance;

    if (y <= this.groundY) {
      // 
      this.y = y;
      if (distance > 0 && !this.isUp) {
        // 20
        this.tween = Hilo.Tween.to(this, { rotation: -20 }, { time: 200 });
        this.isUp = true;
      } else if (distance < 0 && this.isUp) {
        // 90
        this.tween = Hilo.Tween.to(
          this,
          { rotation: 90 },
          { time: this.groundY - this.y }
        );
        this.isUp = false;
      }
    } else {
      // 
      this.y = this.groundY;
      this.isDead = true;
    }
  };
}



